                            <!-- <input value="匯款" type="checkbox" v-model="feemode"> -->
                        </div>
                    </div>
                    <InputOutline :alreadydata="transferaccount" width="327" @inputtexthandler="catchaccountinfo" placeholder="輸入匯款資訊" v-show="feemode.includes('匯款')"></InputOutline>

                </div>

            </div>
            <div class="advinfo-describeBlock">
                <h4>活動描述</h4>
                <textarea id="simplemde"></textarea>
                <!-- <vue-simplemde v-model="content" :configs="configs" /> -->
                <!-- <DescribeBlock @contenthandler="catchinputcontent"></DescribeBlock> -->
            </div>
        </div>

    </div>
    <div class="advinfo-btnwrap">
        <fillbtn btnwidth="193" btnheight="57" btnfz="24" btnname="取消" @clickhandler="upstep" class="cancelbtn"></fillbtn>
        <fillbtn btnwidth="193" btnheight="57" btnfz="24" btnname="下一步" @clickhandler="nextstep"></fillbtn>
    </div>
