}
var Toolbar_br = {
    name: "br",
    title: "強迫換行",
    className: "fa fa-level-down fa-rotate-90",
    action: function (editor) {
        var cm = editor.codemirror;
        cm.replaceSelection("<br />");
    }
};
export default {

    data() {
        return {
            tagcontent: '',
            taglist: [],
            posttaglist: '',
            isfree: '',
            feemode: [],
            configs: {
                autofocus: false,
                status: false,
                spellChecker: false,
                renderingConfig: {
                    codeSyntaxHighlighting: true,
                    highlightingTheme: 'atom-one-light'
                },
                lineWrapping: true,
                placeholder: '在此輸入活動描述',

            },
            content: '',
            htmlcontent: ''
        }
    },
    mounted() {

        this.taglist = this.edittaglist;
        this.isfree = this.editeventisfree
        this.feemode = this.editfeemode;

        let tagpostdata = {
            datatype: 'tag',
            data: this.$store.state.editeventinfo[9]
        }

        let describepostdata = {
            datatype: 'describe',
            data: this.$store.state.editeventinfo[11]
        }

        let posteditprice = {
            datatype: 'price',
            data: this.$store.state.editeventinfo[13]
        }

        let posteditisfree = {
            datatype: 'fee',
            data: this.$store.state.editeventinfo[12]
        }

        let editaccountinfo = {
            datatype: 'accountinfo',
            data: this.$store.state.editeventinfo[15]
        }
        let editchargemethods = {
            datatype: 'chargemethods',
            data: this.$store.state.editeventinfo[14]
        }
        this.$store.commit('setactinfo', tagpostdata)
        this.$store.commit('setactinfo', describepostdata)
        this.$store.commit('setactinfo', posteditprice)
        this.$store.commit('setactinfo', posteditisfree)
        this.$store.commit('setactinfo', editaccountinfo)
        this.$store.commit('setactinfo', editchargemethods)

        var simplemde = new SimpleMDE({
            el: document.getElementById('simplemde'),
            autofocus: false,
            status: false,
            spellChecker: false,
            renderingConfig: {
                codeSyntaxHighlighting: true,
                highlightingTheme: 'atom-one-light'
            },
            lineWrapping: true,
            placeholder: '在此輸入活動描述',
            toolbar: [Toolbar_Base['heading'],
                Toolbar_Base['bold'],
                Toolbar_Base['italic'],
                Toolbar_Base['link'], Toolbar_Base['image'],
                "|", Toolbar_Base['ordered-list'],
                Toolbar_Base['unordered-list'],
                Toolbar_Base['horizontal-rule'], "|",
                Toolbar_Base['undo'],
                Toolbar_Base['redo'],
                "|",
                Toolbar_Base['preview'],
                Toolbar_Base['fullscreen'],
                Toolbar_br,
            ]
        })
        simplemde.value(this.editeventdescribe)
        simplemde.codemirror.on("change", () => {
            this.content = simplemde.value()
            // simplemde.markup(simplemde.value())
            this.htmlcontent = SimpleMDE.prototype.markdown(this.content)
        })
    },
    methods: {

        addtag() {
            if (this.taglist.length < 3) {
                this.taglist.push(this.tagcontent)
                this.tagcontent = ''
            } else {
                alert('一次只能加三個標籤歐！')
            }

        },
        checkactfee() {
            let postdata = {
                datatype: 'fee',
                data: ''
            }
            if (this.isfree == 'true') {
                console.log(this.isfree)
                postdata.data = '是'
            } else if (this.isfree == 'false') {
                console.log(this.isfree)
                postdata.data = '否'
            }
            this.$store.commit('setactinfo', postdata)
        },
        catchvalue(val) {
            console.log('有街道',val)
            if (this.feemode.includes(val)) {
                let index = this.feemode.indexOf(val)
                this.feemode.splice(index, 1)
            } else {
                this.feemode.push(val);
            }
            let chargemethodscontent = ''
            for (let i = 0; i < this.feemode.length; i++) {
                chargemethodscontent = chargemethodscontent + this.feemode[i] + '、'
            }
            chargemethodscontent = chargemethodscontent.substring(0, chargemethodscontent.length - 1)
            let postdata = {
                datatype: 'chargemethods',
                data: chargemethodscontent
            }
            this.$store.commit('setactinfo', postdata)

        },
        catchaccountinfo(val) {
            let postdata = {
                datatype: 'accountinfo',
                data: val
            }
            this.$store.commit('setactinfo', postdata)
        },
        catchprice(val) {
            let postdata = {
                datatype: 'price',
                data: val
            }
            this.$store.commit('setactinfo', postdata)
        },
        upstep() {
            this.$store.commit('minuscreateactindex')
        },
        nextstep() {
            this.$store.commit('addcreateactindex')

            //處理taglist轉為字串，呼叫mutation去新增資料
            let posttagcontent = ''
            for (let i = 0; i < this.taglist.length; i++) {
                posttagcontent = posttagcontent + this.taglist[i] + '、'
            }
            posttagcontent = posttagcontent.substring(0, posttagcontent.length - 1)
            let postdata = {
                datatype: 'tag',
                data: posttagcontent
            }
            if (this.editeventdescribe != '') {
                this.htmlcontent = this.editeventdescribe
            }
            let postdata2 = {
                datatype: 'describe',
                data: this.htmlcontent
            }
            this.$store.commit('setactinfo', postdata)
            this.$store.commit('setactinfo', postdata2)
        },
        deletage(index) {
            this.taglist.splice(index, 1)
        }
    },
    computed: {
        edittaglist() {
            let arr = []
            if (this.$store.state.editeventinfo.length == 0) {
                arr = []
            } else {
                arr = this.$store.state.editeventinfo[9].split('、')
            }
            return arr

        },
        editeventisfree() {
            let isfree
            if (this.$store.state.editeventinfo[12] == '是') {
                isfree = 'true'
            } else {
                isfree = 'false'
            }
            return isfree
        },
        editeventfee() {
            let arr = ''
            if (this.$store.state.editeventinfo.length == 0) {
                arr = ''
            } else {
                arr = this.$store.state.editeventinfo[13]
            }
            return arr
        },
        Onsitepayment() {
            let status
            if (this.$store.state.editeventinfo.length == 0) {
                status = ''
            } else {
                if (this.$store.state.editeventinfo[14].includes("協會現場繳費")) {
                    status = true
                } else {
                    status = false
                }
            }

            return status

        },
        transferpayment() {
            let status
            if (this.$store.state.editeventinfo.length == 0) {
                status = ''
            } else {
                if (this.$store.state.editeventinfo[14].includes("匯款")) {
                    status = true
                } else {
                    status = false
                }
            }

            return status
        },
        editfeemode() {
            let status
            if (this.$store.state.editeventinfo.length == 0) {
                status = []
            } else {
                status = this.$store.state.editeventinfo[14].split('、')
            }
            return status
        },
        transferaccount() {
            let arr = ''
            if (this.$store.state.editeventinfo.length == 0) {
                arr = ''
            } else {
                arr = this.$store.state.editeventinfo[15]
            }
            return arr
        },
        editeventdescribe() {
            let arr = ''
            if (this.$store.state.editeventinfo.length == 0) {
                arr = ''
            } else {
                arr = this.$store.state.editeventinfo[11]
            }

            return arr

        }
    },
    components: {
        fillbtn,
        InputOutline,
        // VueSimplemde,
        CheckBox
    }
}
</script>

